import { CustomerService } from './../../../services/customer.service';
import { Customer } from './../../../models/customer.model';
import { ErrorStateMatcher } from '@angular/material/core';
import { FormControl, FormGroupDirective, NgForm, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
import { MouseEvent } from '@agm/core';
export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}
@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.scss']
})
export class RegistrationComponent implements OnInit {

  emailFormControl = new FormControl('', [
    Validators.required,
    Validators.email,
  ]);
  hide = true;
  matcher = new MyErrorStateMatcher();
  // customer = new Customer();
  customer: Customer;
  markers = [];
  zoom = 1;
  ngOnInit(): void {
    this.customer = {
      name: '',
      email: '',
      password: '',
      birthday: '',
      gender: '1',
      area: '',
      language: 'vi',
      address: '',
      longitude: '',
      latitude: '',
      phone: '',
    };
  }

  constructor(
    private customerService: CustomerService,
    private toastr: ToastrService,
    private router: Router
  ) {

  }
  autoCompleteCallback(event: any) {
    console.log(event);
    if (event.data) {
      this.customer.latitude = event.data.geometry.location.lat;
      this.customer.longitude = event.data.geometry.location.lng;
      this.zoom = 15;
      this.markers = [];
      this.markers.push({
        lat: event.data.geometry.location.lat,
        lng: event.data.geometry.location.lng,
      });
      event.data.address_components.forEach(el => {
        if (el.long_name === 'Hồ Chí Minh' || el.long_name === 'Ho Chi Minh City') {
          this.customer.area = 'HCM';
          return;
        }
        if (el.long_name === 'Vũng Tàu' || el.long_name === 'Ba Ria - Vung Tau'
          || el.long_name === 'Bà Rịa - Vũng Tàu' || el.long_name === 'Ba Ria') {
          this.customer.area = 'VT';
          return;
        }
      });
    }
  }
  onSubmit() {
    if (this.customer.birthday) {
      this.customer.birthday = new Date(this.customer.birthday).toISOString();
    }
    const elSearch: any = document.getElementById('search_places');
    if (elSearch) {
      this.customer.address = elSearch.value;
    }

    console.log(this.customer);
    this.customerService.customerRegister(this.customer).subscribe(result => {
      if (result.message === 'Error') {
        this.toastr.error(result.message, 'Something went wrong please try again!', {
          progressBar: true,
          positionClass: 'toast-bottom-right',
          timeOut: 2000
        });
      } else {
        this.toastr.success('', result.message, {
          progressBar: true,
          positionClass: 'toast-bottom-right',
          timeOut: 3000
        });
      }
    });
  }
  mapClicked(event: MouseEvent) {
    this.markers = [];
    this.markers.push({
      lat: event.coords.lat,
      lng: event.coords.lng
    });
    this.customer.latitude = String(event.coords.lat);
    this.customer.longitude = String(event.coords.lng);
  }
  markerDragEnd(event: MouseEvent) {
    this.markers = [];
    this.markers.push({
      lat: event.coords.lat,
      lng: event.coords.lng
    });
    this.customer.latitude = String(event.coords.lat);
    this.customer.longitude = String(event.coords.lng);
  }

}
