import { ResetPasswordComponent } from './layout/account/resetPassword/resetPassword.component';
import { RewardsComponent } from './core/shopping/rewards/rewards.component';
import { MilkComponent } from './core/shopping/milk/milk.component';
import { BeefComponent } from './core/shopping/beef/beef.component';
import { ShoppingComponent } from './core/shopping/shopping.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './layout/account/login/login.component';
import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { MainComponent } from './layout/main/main.component';
import { RegistrationComponent } from './layout/account/registration/registration.component';
import { UserGuard } from './Guard/user.guard';
import { OrdersComponent } from './core/user/orders/orders.component';
import { ProfilesComponent } from './core/user/profiles/profiles.component';
import { SettingsComponent } from './core/user/settings/settings.component';

const routes: Routes = [
  { path: 'signup', component: RegistrationComponent },
  { path: 'signin', component: LoginComponent },
  { path: 'resetPassword', component: ResetPasswordComponent },
  {
    path: 'app', component: MainComponent, canActivate: [UserGuard], children: [
      { path: '', redirectTo: '/app/shopping/beef', pathMatch: 'full' },
      {
        path: 'shopping', component: ShoppingComponent, children: [
          { path: '', redirectTo: '/app/shopping/beef', pathMatch: 'full' },
          { path: 'beef', component: BeefComponent },
          { path: 'milk', component: MilkComponent },
          { path: 'rewards', component: RewardsComponent },
        ]
      },
      { path: 'orders', component: OrdersComponent },
      { path: 'profiles', component: ProfilesComponent },
      { path: 'settings', component: SettingsComponent },
    ]
  },
  { path: '**', redirectTo: '/app/shopping/beef' },

];

@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    RouterModule.forRoot(routes)
  ],
  exports: [
  ],
})
export class AppRoutingModule { }


