import { SatPopoverModule } from '@ncstate/sat-popover';
import { ContenteditableDirective } from 'ng-contenteditable';
import { LoadingService } from './services/loading.service';
import { CustomerService } from './services/customer.service';
import { ApiService } from './services/api.service';
import { SidenavComponent } from './layout/sidenav/sidenav.component';
import { ToastrModule } from 'ngx-toastr';
import { MilkComponent } from './core/shopping/milk/milk.component';
import { SocialLoginModule, AuthServiceConfig } from 'angularx-social-login';
import { GoogleLoginProvider, FacebookLoginProvider, LinkedInLoginProvider } from 'angularx-social-login';
import { OrderService } from './services/order.service';
import { UserService } from './services/user.service';
import { OrdersComponent } from './core/user/orders/orders.component';
import { ProfilesComponent } from './core/user/profiles/profiles.component';
import { SettingsComponent } from './core/user/settings/settings.component';
