import { ProductService } from './services/product.service';
import {TranslateModule, TranslateLoader} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';

const config = new AuthServiceConfig([
  {
    id: GoogleLoginProvider.PROVIDER_ID,
    provider: new GoogleLoginProvider('1055086183299-r9g9qjkgh5nk7ur9tkt8g3olf2hkr7ju.apps.googleusercontent.com')
  },
  {
    id: FacebookLoginProvider.PROVIDER_ID,
    provider: new FacebookLoginProvider('207310030026271')
  }
]);

// AoT requires an exported function for factories
export function HttpLoaderFactory(httpClient: HttpClient) {
  return new TranslateHttpLoader(httpClient);
}

export function provideConfig() {
  return config;
}
const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
  suppressScrollX: true,
  wheelSpeed: 2,
  wheelPropagation: true,
};

@NgModule({
  declarations: [
    AppComponent,
    SpinnerComponent,
    LoginComponent,
    MainComponent,
    SearchComponent,
    RegistrationComponent,
    ContenteditableDirective,
    SidenavComponent,
    // shopping
    ShoppingComponent,
    BeefComponent,
    MilkComponent,
    RewardsComponent,
    // user
    OrdersComponent,
    ProfilesComponent,
    SettingsComponent,
    ResetPasswordComponent,

    // Dialog
    DialogOrderDetailsComponent,
    DialogDetailBeefComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    MaterialModule,
    PerfectScrollbarModule,
    RouterModule,
    HttpClientModule,
    HttpModule,
    AppRoutingModule,
    FormsModule,
    SatPopoverModule,
    FlexLayoutModule,
    ReactiveFormsModule,
    SocialLoginModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    }),
    ToastrModule.forRoot({
    }),
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyB2F-EqU6Ok8jJWjv8J6XwesL0QmmxjqOk'
    }),
    Ng4GeoautocompleteModule.forRoot(),
  ],
  providers: [
    {
      provide: PERFECT_SCROLLBAR_CONFIG,
      useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG
    },
    UserGuard,
    AuthenticationService,
    LoadingService,
    ApiService,
    CustomerService,
    OrderService,
    ProductService,
    UserService,
    {
      provide: AuthServiceConfig,
      useFactory: provideConfig
    }
  ],
  entryComponents: [
    DialogOrderDetailsComponent,
    DialogDetailBeefComponent
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
