
import { throwError as observableThrowError, Observable } from 'rxjs';

import { map, catchError } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { error } from 'util';
import { environment } from '../../environments/environment';
import { Headers, Http, Response, URLSearchParams } from '@angular/http';

@Injectable()
export class ApiService {

  constructor(private http: Http) { }

  private setHeaders(): Headers {
    const headersConfig = {
      'Content-Type': 'application/json',
      'Accept': 'application/json',
    };
    return new Headers(headersConfig);
  }

  // tslint:disable-next-line:no-shadowed-variable
  private formatErrors(error: any) {
    return observableThrowError(error.json());
  }

  get(path: string): Observable<any> {
    // tslint:disable-next-line:no-shadowed-variable
    return this.http.get(`${environment.api_url}${path}`).pipe(catchError(this.formatErrors),
      map((res: Response) => res.json()), );
  }
  getQuery(path: string, params: URLSearchParams = new URLSearchParams()): Observable<any> {
    return this.http.get(`${environment.api_url}${path}`, { headers: this.setHeaders(), search: params }).pipe(
      catchError(this.formatErrors),
      map((res: Response) => res.json()), );
  }

  put(path: string, body: any = {}): Observable<any> {
    return this.http.put(`${environment.api_url}${path}`,
      JSON.stringify(body),
      // tslint:disable-next-line:no-shadowed-variable
      { headers: this.setHeaders() }).pipe(catchError(this.formatErrors),
        map((res: Response) => res.json()), );
  }
  post(path: string, body: any = {}): Observable<any> {
    return this.http.post(`${environment.api_url}${path}`,
      JSON.stringify(body), { headers: this.setHeaders() }).pipe(catchError(this.formatErrors),
        map((res: Response) => res.json()), );
  }

  delete(path): Observable<any> {
    return this.http.delete(
      `${environment.api_url}${path}`,
      { headers: this.setHeaders() }).pipe(catchError(this.formatErrors),
        map((res: Response) => res.json()), );
  }
  // setDataRequest(data: any): any {
  //   return {
  //     AppId: environment.request.AppId,
  //     Signature: environment.request.Signature,
  //     Data: data
  //   };
  // }
}
