
import {map} from 'rxjs/operators';
import { Injectable } from '@angular/core';
// tslint:disable-next-line:import-blacklist
import { Observable ,  BehaviorSubject } from 'rxjs';


import { URLSearchParams } from '@angular/http';
import { ApiService } from './api.service';

@Injectable()
export class OrderService {
    constructor(
        private apiService: ApiService
    ) { }
    getListCyberPassport(data): Observable<any> {
        return this.apiService.post('/ws/api/v01/order/getListCyberPassportByCommodityTypeCde', data).pipe(
            map(result => result));
    }
    getbCodeBycPassAndArea(data): Observable<any> {
        return this.apiService.post('/ws/api/v01/order/getBlochainCodeByCyberPassCdeAndAreaBranchCde', data).pipe(
            map(result => result));
    }
    sendPurchaseOrders(data): Observable<any> {
        return this.apiService.post('/customer/api/v01/purchaseOrders/sendPurchaseOrders', data).pipe(
            map(result => result));
    }
    getListRewardPointsOfArea(data): Observable<any> {
        return this.apiService.post('/ws/api/v01/order/getListRewardPointsCommodityOfArea', data).pipe(
            map(result => result));
    }
}
