import { FormControl, Validators, FormGroupDirective, NgForm, FormGroup } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ErrorStateMatcher } from '@angular/material/core';
import { CustomerService } from '../../../services/customer.service';
import { AuthService } from 'angularx-social-login';
import { FacebookLoginProvider, GoogleLoginProvider, LinkedInLoginProvider } from 'angularx-social-login';
import { ToastrService } from 'ngx-toastr';
import { LoadingService } from '../../../services/loading.service';
export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  // Hai
  private user;
  // Hai
  matcher = new MyErrorStateMatcher();

  formLogin: FormGroup;
  // Hai private authService: AuthService
  constructor(private customerService: CustomerService,
    private router: Router, private authService: AuthService,
    private toastr: ToastrService,
    public loadingService: LoadingService
  ) {
    this.loadingService.stop();
  }
  // Hai
  signInWithGoogle(): void {
    this.authService.signIn(GoogleLoginProvider.PROVIDER_ID);
  }

  signInWithFB(): void {
    this.authService.signIn(FacebookLoginProvider.PROVIDER_ID);
  }
  // Hai
  ngOnInit(): void {
    this.formLogin = new FormGroup({
      email: new FormControl('', [Validators.required, Validators.email]),
      password: new FormControl('', [Validators.required])
    });
    this.authService.authState.subscribe((user) => {
      this.user = user;
    });
    //   const data = {
    //     CustPassword: '123456789',
    //     CustEmail: 'manhtung321@gmail.com'
    // };
    // this.customerService.customerLogin(data).subscribe(result => {
    //     console.log(result);
    // });

  }



  onSubmit() {
    this.loadingService.start();
    setTimeout(() => {
      if (this.formLogin.valid) {
        this.customerService.customerLogin(this.formLogin.value).subscribe(result => {
          if (result.message === true) {
            localStorage.setItem('CurrentUser', JSON.stringify(result.data[0]));
            this.loadingService.stop();
            this.router.navigate(['/app']);
          } else {
            this.loadingService.stop();
            this.toastr.error('Login False!', 'Tài khoản đăng nhập không chính xác...', {
              progressBar: true,
              positionClass: 'toast-bottom-right',
              timeOut: 2000
            });
          }
        });
      } else {
        this.loadingService.stop();
      }
    }, 1000);
  }

}
